




























@import "@/styles/main.scss";

.Cover {
  // z-index: -1;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  background-color: $primary;
  // position: sticky;
  // top: 0;
  height: 50vh;
  text-align: center;
  color: $white;
  @include media(s1) {
    height: 75vh;
  }
  @include media(s2) {
    height: 100vh;
  }
}

.CoverTitle {
  // margin: 0;
}
.CoverSubtitle {
  margin-right: 0;
}
