





























































@import "@/styles/main.scss";

.SocialLinks {
  padding: 0;
  li {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  a {
    // margin-right: $h0;
    @include media(s1) {
      // margin-right: $h2;
      width: $h3;
    }
    svg {
      width: $h2;
      @include media(s0) {
        width: $h1;
      }
      path,
      rect,
      polygon {
        fill: $primary;
        &:hover {
          fill: $grey;
        }
      }
    }
  }
}
